import axios from 'axios'

export const getTaskParents = (params) => {
    return axios.request({
      url: 'etl/get-task-parents',
      method: 'GET',
      params: params
    })
  };
  export const getTaskGraph = (data) => {
    return axios.request({
      url: 'etl/query-relation-graph',
      method: 'POST',
      data: data
    })
  };
  export const getAllLeafTask = () => {
    return axios.request({
      url: 'etl/query-leaf-etl',
      method: 'GET'
    })
  };
  export const getTaskParentsWithoutFilter = (params) => {
    return axios.request({
      url: 'etl/get-task-parents-without-filter',
      method: 'GET',
      params: params
    })
  };
export const getEtlTask = () => {
    return axios.request({
      url: 'etl/get-etl-tasks',
      method: 'GET',
    })
  };
export const getEtlTaskReadyTime = () => {
    return axios.request({
      url: 'etl/query-task-ready-time',
      method: 'GET',
    })
  };
export const addTask = (taskName) => {
  return axios.post(
    'etl/add-task',
    null,
    {params: {
      taskName
    }}
  )
};
export const addTaskDepend = (taskName) => {
  return axios.post(
    'etl/add-task-depend',
    null,
    {params: {
      taskName
    }}
  )
};
export const addReadyTask = (taskName) => {
  return axios.post(
    'etl/update-ready-time',
    null,
    {params: {
      taskName
    }}
  )
};
export const addWhiteList = (taskName) => {
  return axios.post(
    'etl/add-white-list',
    null,
    {params: {
      taskName
    }}
  )
};
export const removeWhiteList = (taskName) => {
  return axios.post(
    'etl/remove-white-list',
    null,
    {params: {
      taskName
    }}
  )
};
export const getWhiteList = () => {
  return axios.request({
    url: 'etl/get-white-list',
    method: 'GET',
  })
};
export const updateRemark = (taskName, remark) => {
  return axios.post(
    'etl/update-task-remark',
    null,
    {
      params: {
        taskName,
        remark
      }
    }
  )
};