<template>
  <div id="app">
    <el-container>
      <el-header>

      </el-header>
      <el-main>
        <el-card class="box-card">
      <div class="text item" style="text-align: left;">{{cost}}{{num}}</div>
    </el-card>
        <el-table
      :data="tableData"
      border
      :row-class-name="tableRowClassName"
      style="width: 100%"
    >
      <el-table-column prop="first_cost" label="" width="200"></el-table-column>
      <el-table-column prop="num" label="" width="200">
      </el-table-column>
      <el-table-column prop="second_cost" label="" width="200"></el-table-column>
      <el-table-column prop="use" label="" width="200"></el-table-column>
      <el-table-column prop="cost" label="" width="200"></el-table-column>
      
    </el-table>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import TaskRelationsData from "@/components/TaskRelationsData.vue";
import TaskAlarmData from "@/components/TaskAlarmData.vue";
import TaskGraph from "@/components/TaskGraph.vue";

export default {
  name: "Main",
  components: {
    TaskRelationsData,
    TaskAlarmData,
    TaskGraph
  },
  data() {
    return {
      tableData: [],
      target: 20000,
      bonus: 999,
      discountLimit: 300,
      discount: 80,
      cost: 0,
      num: 0
    };
  },
  mounted() {
     this.refresh();
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (row["min"]) {
        return "warning-row";
      }
      return "";
    },
    refresh() {
      let discount_cost = this.discountLimit - this.discount;
     let min = Number.MAX_VALUE;
     let bestNum = 1;
      for (let num = 1;num * this.bonus < this.target;num++) {
          let remain = this.target - this.bonus * num;
          let discountNum = Math.floor(remain / this.discountLimit);
          
          if (discountNum > num) {
              discountNum = num;
          }

          remain = remain - discountNum * this.discountLimit;
          let cost = this.bonus * num + discount_cost * discountNum + remain;
          if (cost < min) {
              min = cost;
              bestNum = num;
          }
        
          this.tableData.push({
            first_cost: num * this.bonus,
            num: num,
            second_cost: discount_cost * discountNum + remain,
            use: discountNum,
            cost: cost,
            min: false
          });
      }
      this.cost = this.tableData[bestNum - 1].cost;
      this.num = this.tableData[bestNum - 1].num;
      this.tableData[bestNum - 1].min = true;
      console.log(this.tableData)

    }
  }
  
  
};
</script>

