<template>
  <div>
    <el-card class="box-card">
      <div class="text item">1.Cantor API</div>
      <div class="text item">2.30dailyETL</div>
      <div
        class="text item"
      >3.(Q1)(Q3)Q3+k(Q3-Q1)k1.5</div>
    </el-card>
    <el-row :gutter="2">
      <el-col :offset="22" :span="2">
        <el-button @click="addTask">ETL</el-button>
      </el-col>
    </el-row>

    <el-table
      :data="tableData"
      border
      :row-class-name="tableRowClassName"
      @cell-click="showEditRemark"
      style="width: 100%"
    >
      <el-table-column prop="taskname" label=""></el-table-column>
      <el-table-column prop="remark" label="" width="280">
        <template slot-scope="scope">
          <el-input
            v-model="scope.row.remark"
            placeholder=""
            v-show="scope.row.showEdit"
            :ref="'remark'+scope.$index"
            @blur="handleUpdate(scope.$index, scope.row)"
            @keyup.enter.native="handleUpdate(scope.$index, scope.row)"
          ></el-input>
          <span v-show="!scope.row.showEdit">{{scope.row.remark}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="expectStartTime" label="" width="280"></el-table-column>
      <el-table-column prop="expectReadyTime" label="" width="280"></el-table-column>
      <el-table-column width="180" label="">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="showLine(scope.$index, scope)"
            type="text"
            size="small"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-drawer title="" :visible.sync="drawer" size="60%" :with-header="false">
      <div>
        <echart
          ref="chart-start"
          :options="startOptions"
          :auto-resize="true"
          class="drawer-content"
        ></echart>
      </div>
      <div style="margin-top:50px">
        <echart
          ref="chart-ready"
          :options="readyOptions"
          :auto-resize="true"
          class="drawer-content"
        ></echart>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import {
  getTaskParents,
  getEtlTask,
  addReadyTask,
  updateRemark,
  getEtlTaskReadyTime
} from "@/service/getData";

import VueSvgTree from "@/components/SvgTree.vue";

export default {
  components: {
    VueSvgTree
  },
  data() {
    return {
      activeIndex: "1",
      statusMap: {
        0: "",
        1: "",
        2: "",
        3: "",
        4: "",
        13: ""
      },
      input: "",
      treeData: [],
      tableData: [],
      startOptions: {
        xAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              var date = new Date(value);
              var texts = [date.getMonth() + 1, date.getDate()];

              return texts.join("/");
            }
          },
          boundaryGap: ["30%", "10%"]
        },
        yAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              return this.secondToHour(value);
            }
          }
        },
        series: [
          {
            data: [],
            type: "line"
          }
        ]
      },
      readyOptions: {
        xAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              var date = new Date(value);
              var texts = [date.getMonth() + 1, date.getDate()];

              return texts.join("/");
            }
          },
          boundaryGap: ["30%", "10%"]
        },
        yAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              return this.secondToHour(value);
            }
          }
        },
        series: [
          {
            data: [],
            type: "line"
          }
        ]
      },
      drawer: false
    };
  },
  async mounted() {
    await this.initTaskReadyTime();
    if (this.$route.query.taskName != null) {
      this.tableData.forEach(data => {
        if (data.taskname == this.$route.query.taskName) {
          console.log("route data", data);
          let scope = {
            row: data
          };
          this.showLine(0, scope);
        }
      });
    }
  },
  methods: {
    async initTaskReadyTime() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      await getEtlTaskReadyTime()
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            let data = res.data.data;
            if (data != null && data != undefined) {
              this.tableData = [];
              data.forEach(taskReadyTime => {
                let tableReadyTime = {};
                tableReadyTime["taskname"] = taskReadyTime["taskName"];
                let expectStartTimeStamp = taskReadyTime["expectStartTime"];
                let expectReadyTimeStamp = taskReadyTime["expectReadyTime"];
                tableReadyTime["expectStartTime"] = this.secondToHour(
                  taskReadyTime["expectStartTime"]
                );
                tableReadyTime["expectReadyTime"] = this.secondToHour(
                  taskReadyTime["expectReadyTime"]
                );

                let readyTimes = [];
                let startTimes = [];
                let expectReadyTimes = [];
                let expectStartTimes = [];
                taskReadyTime["readyTimes"].forEach(readyTime => {
                  readyTimes.push([readyTime.dateTime, readyTime.readyTime]);
                  startTimes.push([readyTime.dateTime, readyTime.startTime]);
                  expectReadyTimes.push([
                    readyTime.dateTime,
                    expectReadyTimeStamp
                  ]);
                  expectStartTimes.push([
                    readyTime.dateTime,
                    expectStartTimeStamp
                  ]);
                });
                tableReadyTime["readyTimeData"] = readyTimes;
                tableReadyTime["startTimeData"] = startTimes;
                tableReadyTime["expectReadyTimes"] = expectReadyTimes;
                tableReadyTime["expectStartTimes"] = expectStartTimes;

                this.tableData.push(tableReadyTime);
              });
            }
          } else {
            this.$message.error("");
          }
          loading.close();
        })
        .catch(error => {
          loading.close();
          this.$message.error("");
          console.error(error);
        });
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (row["hasWeakRelation"]) {
        return "warning-row";
      }
      return "";
    },
    showLine(index, scope) {
      console.log(scope);
      this.startOptions = {
        title: {
          text: "30",
          left: "center"
        },
        tooltip: {
          triggerOn: "mousemove",
          formatter: value => {
            console.log(value);
            var date = new Date(value.data[0] * 1000);
            var texts = [date.getMonth() + 1, date.getDate()];
            var dateStr = texts.join("/");

            var time = this.secondToHour(value.data[1]);
            return dateStr + "\n" + time;
          }
        },
        legend: {
          bottom: 0
        },
        xAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              var date = new Date(value * 1000);
              var texts = [date.getMonth() + 1, date.getDate()];

              return texts.join("/");
            }
          },
          boundaryGap: ["30%", "10%"]
        },
        yAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              return this.secondToHour(value);
            }
          }
        },
        series: [
          {
            name: "",
            data: scope.row.startTimeData,
            type: "line"
          },
          {
            name: "",
            data: scope.row.expectStartTimes,
            type: "line",
            symbol: "none"
          }
        ]
      };
      this.readyOptions = {
        title: {
          text: "30",
          left: "center"
        },
        tooltip: {
          triggerOn: "mousemove",
          axisPointer: {
            snap: true
          },
          formatter: value => {
            console.log(value);
            var date = new Date(value.data[0] * 1000);
            var texts = [date.getMonth() + 1, date.getDate()];
            var dateStr = texts.join("/");

            var time = this.secondToHour(value.data[1]);
            return dateStr + "<br />" + time;
          }
        },
        
        legend: {
          bottom: 0
        },
        xAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              var date = new Date(value * 1000);
              var texts = [date.getMonth() + 1, date.getDate()];

              return texts.join("/");
            }
          },
          boundaryGap: ["30%", "10%"]
        },
        yAxis: {
          type: "time",
          axisLabel: {
            formatter: value => {
              return this.secondToHour(value);
            }
          }
        },

        series: [
          {
            name: "",
            data: scope.row.readyTimeData,
            type: "line"
          },
          {
            name: "",
            data: scope.row.expectReadyTimes,
            type: "line",
            symbol: "none"
          }
        ]
      };
      console.log(this.orgOptions);
      this.drawer = true;
    },
    showEditRemark(row, column, cell, event) {
      console.log(row, column, cell, event);
      if (!row.showEdit && column.property == "remark") {
        row.showEdit = true;
        this.$set(this.tableData, row.index, row);
        console.log("refs", this.$refs[`remark${row.index}`]);
        this.$nextTick(function() {
          this.$refs[`remark${row.index}`].$el.querySelector("input").focus();
        });
        this.$set(this.tableData, row.index, row);
      }
    },
    handleUpdate(index, row) {
      console.log(row);
      row.showEdit = false;
      if (row.taskname != null && row.remark != null)
        updateRemark(row.taskname, row.remark)
          .then(res => {
            console.log(res);
            if (res.data.code === 0) {
              this.$message.success("");
            } else {
              this.$message.error("");
            }
          })
          .catch(error => {
            this.$message.error("");
            console.error(error);
          });
      this.$set(this.tableData, row.index, row);
    },
    time() {
      var time = new Date();
      var y = time.getFullYear();
      var M = time.getMonth() + 1;
      var d = time.getDate();
      var h = time.getHours();
      var m = time.getMinutes();
      var s = time.getSeconds();
      return (
        y +
        "-" +
        this.addZero(M) +
        "-" +
        this.addZero(d) +
        " " +
        this.addZero(h) +
        ":" +
        this.addZero(m) +
        ":" +
        this.addZero(s)
      );
    },
    addTask() {
      this.$prompt("ETL", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          if (value != null) {
            const loading = this.$loading({
              lock: true,
              text: "Loading",
              spinner: "el-icon-loading",
              background: "rgba(0, 0, 0, 0.7)"
            });
            addReadyTask(value)
              .then(res => {
                console.log(res);
                if (res.data.code === 0) {
                  this.initTaskReadyTime();
                  this.$message.success("");
                  this.$set(this.tableData, 0, this.tableData[0]);
                } else {
                  this.$message.error("");
                }
                loading.close();
              })
              .catch(error => {
                this.$message.error("");
                console.error(error);
                loading.close();
              });
          }
        })
        .catch(error => {
          console.error(error);
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    addZero(m) {
      return m < 10 ? "0" + m : m;
    },
    secondToHour(value) {
      let hour = Math.floor(value / 3600);
      let minute = Math.floor((value - hour * 3600) / 60);
      let second = value - hour * 3600 - minute * 60;
      let texts = [
        this.addZero(hour),
        this.addZero(minute),
        this.addZero(second)
      ];

      return texts.join(":");
    }
  }
};
</script>

