<template>
  <div>
    <el-card class="box-card">
      <div class="text item">1.Cantor API</div>
      <div class="text item">2./</div>
      <div class="text item">3.xt</div>
      <div class="text item">4.</div>
      <div class="text item">5.</div>
    </el-card>

    <el-row :gutter="2">
      <el-col :span="2" :offset="16">
        <el-dropdown>
          <el-button>
            
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>hmart_waimaiuser</el-dropdown-item>
            <el-dropdown-item>hmart_waimai_bizdata</el-dropdown-item>
            <el-dropdown-item>hmart_waimaibizdata</el-dropdown-item>
            <el-dropdown-item>hadoop-waimaibizdata</el-dropdown-item>
            <el-dropdown-item>hadoop-hmart-waimaiuser</el-dropdown-item>
            <el-dropdown-item>mart_waimaibizdatacollect_bizdatacollect</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col :span="2">
        <el-dropdown @command="handleCommand">
          <el-button>
            
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              v-for="(item,index) in whiteDropList"
              :command="item.name"
              :key="index"
            >
              {{item.name}}
              <i class="el-icon-delete"></i>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col :span="2">
        <el-button @click="addTask">ETL</el-button>
      </el-col>
    </el-row>

    <el-table
      :data="tableData"
      border
      :row-class-name="tableRowClassName"
      @cell-click="showEditRemark"
      style="width: 100%"
    >
      <el-table-column prop="taskname" label=""></el-table-column>
      <el-table-column prop="remark" label="" width="200">
        <template slot-scope="scope">
          <el-input
            v-model="scope.row.remark"
            placeholder=""
            v-show="scope.row.showEdit"
            :ref="'remark'+scope.$index"
            @blur="handleUpdate(scope.$index, scope.row)"
            @keyup.enter.native="handleUpdate(scope.$index, scope.row)"
          ></el-input>
          <span v-show="!scope.row.showEdit">{{scope.row.remark}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="utime" label="" width="200"></el-table-column>
      <el-table-column width="340" label="">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="showTree(scope.$index, scope)"
            type="text"
            size="small"
          ></el-button>
          <el-button
            @click.native.prevent="refreshTree(scope.$index, scope)"
            type="text"
            size="small"
          ></el-button>
          <el-button
            @click.native.prevent="addWhiteList(scope.$index, scope)"
            type="text"
            size="small"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-drawer title="" :visible.sync="drawer" size="80%" :with-header="false">
      <div>
        <VueSvgTree :treeData="treeData" direction="row" svgId="svg" ref="svgTree"></VueSvgTree>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import {
  getTaskParents,
  getTaskParentsWithoutFilter,
  getEtlTask,
  addTask,
  updateRemark,
  addWhiteList,
  removeWhiteList,
  getWhiteList
} from "@/service/getData";

import VueSvgTree from "@/components/SvgTree.vue";

export default {
  components: {
    VueSvgTree
  },
  data() {
    return {
      activeIndex: "1",
      statusMap: {
        0: "",
        1: "",
        2: "",
        3: "",
        4: "",
        13: ""
      },
      input: "",
      treeData: [],
      tableData: [],
      whiteDropList: [],
      whiteList: [],
      drawer: false
    };
  },
  async mounted() {
    this.getWhiteList();
    await this.initTask();

    if (this.$route.query.taskName != null) {
      this.tableData.forEach(data => {
        if (data.taskname == this.$route.query.taskName) {
          console.log("route data", data);
          this.treeData = data.treeData;
          this.drawer = true;
        }
      });
    }
  },
  methods: {
    initData() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      let params = {
        taskName:
          "mart_waimaibizdatacollect_bizdatacollect.wm_bizdata_poly_map_dis_aor_v4"
      };
      console.log(params);
      getTaskParents(params)
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            let me = this;
            let data = res.data.data;
            let queue = [];
            let id = 1;
            data["id"] = id++;
            queue.push(data);
            this.treeData = [];
            this.treeData.push({
              id: data["id"],
              taskName: data["taskName"],
              fatherId: 0,
              time: data["execTime"],
              status: me.statusMap[data["status"]]
            });
            console.log(this.treeData);
            while (queue.length != 0) {
              let task = queue.shift();
              console.log(task["taskName"], task);
              let parents = task["parents"];
              if (parents != null) {
                parents.forEach(p => {
                  p["id"] = id++;
                  queue.push(p);
                  this.treeData.push({
                    id: p["id"],
                    taskName: p["taskName"],
                    dependType: p["dependType"],
                    color: p["dependType"] == 1 ? "black" : "red",
                    fatherId: task["id"],
                    time: p["execTime"],
                    status: me.statusMap[p["status"]]
                  });
                  console.log(this.treeData);
                });
              }
            }
          } else {
            this.$message.error("");
          }
          loading.close();
        })
        .catch(error => {
          loading.close();
          this.$message.error("");
          console.error(error);
        });
    },
    async initTask() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });

      await getEtlTask()
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            let me = this;
            let data = res.data.data;
            if (data != null && data != undefined) {
              this.tableData = [];
              data.forEach(mainTask => {
                let tableTask = {};
                tableTask["taskname"] = mainTask["taskName"];
                tableTask["remark"] = mainTask["remark"];
                tableTask["utime"] = mainTask["utime"];
                tableTask["hasWeakRelation"] =
                  mainTask["weakRelation"].length > 0 &&
                  this.whiteList.indexOf(mainTask["taskName"]) == -1;
                console.log(
                  "weakRelation",
                  this.whiteList,
                  mainTask["taskName"]
                );
                let queue = [];

                let id = 1;
                mainTask["id"] = id++;
                queue.push(mainTask);
                let innerTreeData = [];
                innerTreeData.push({
                  id: mainTask["id"],
                  taskName: mainTask["taskName"],
                  fatherId: 0,
                  time: mainTask["execTime"],
                  status: me.statusMap[mainTask["status"]]
                });

                while (queue.length != 0) {
                  let task = queue.shift();
                  console.log(task["taskName"], task);
                  let parents = task["parents"];
                  if (parents != null) {
                    parents.forEach(p => {
                      p["id"] = id++;
                      queue.push(p);
                      innerTreeData.push({
                        id: p["id"],
                        taskName: p["taskName"],
                        dependType: p["dependType"],
                        color: p["dependType"] == 1 ? "black" : "red",
                        fatherId: task["id"],
                        time: p["execTime"],
                        status: me.statusMap[p["status"]]
                      });
                    });
                  }
                }

                id = 1;
                queue.push(mainTask);
                let innerAllTreeData = [];
                innerAllTreeData.push({
                  id: mainTask["id"],
                  taskName: mainTask["taskName"],
                  fatherId: 0,
                  time: mainTask["execTime"],
                  status: me.statusMap[mainTask["status"]]
                });
                while (queue.length != 0) {
                  let task = queue.shift();
                  console.log(task["taskName"], task);
                  let parents = task["allParents"];
                  if (parents != null) {
                    parents.forEach(p => {
                      p["id"] = id++;
                      queue.push(p);
                      innerAllTreeData.push({
                        id: p["id"],
                        taskName: p["taskName"],
                        dependType: p["dependType"],
                        color: p["dependType"] == 1 ? "black" : "red",
                        fatherId: task["id"],
                        time: p["execTime"],
                        status: me.statusMap[p["status"]]
                      });
                    });
                  }
                }
                tableTask["treeData"] = innerTreeData;
                tableTask["allTreeData"] = innerAllTreeData;
                this.tableData.push(tableTask);
              });
            }
          } else {
            this.$message.error("");
          }
          loading.close();
        })
        .catch(error => {
          loading.close();
          this.$message.error("");
          console.error(error);
        });
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (row["hasWeakRelation"]) {
        return "warning-row";
      }
      return "";
    },
    showTree(index, scope) {
      console.log(scope);
      this.treeData = scope.row.treeData;
      this.drawer = true;
    },
    showAllTree(index, scope) {
      console.log(scope);
      this.treeData = scope.row.allTreeData;
      this.drawer = true;
    },
    showEditRemark(row, column, cell, event) {
      console.log(row, column, cell, event);
      if (!row.showEdit && column.property == "remark") {
        row.showEdit = true;
        this.$set(this.tableData, row.index, row);
        console.log("refs", this.$refs[`remark${row.index}`]);
        this.$nextTick(function() {
          this.$refs[`remark${row.index}`].$el.querySelector("input").focus();
        });
        this.$set(this.tableData, row.index, row);
      }
    },
    handleUpdate(index, row) {
      console.log(row);
      row.showEdit = false;
      if (row.taskname != null && row.remark != null)
        updateRemark(row.taskname, row.remark)
          .then(res => {
            console.log(res);
            if (res.data.code === 0) {
              this.$message.success("");
            } else {
              this.$message.error("");
            }
          })
          .catch(error => {
            this.$message.error("");
            console.error(error);
          });
      this.$set(this.tableData, row.index, row);
    },
    refreshTree(index, scope) {
      let taskName = scope.row.treeData[0].taskName;

      let params = {
        taskName: taskName
      };
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      console.log(params);
      getTaskParents(params)
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            let me = this;
            let data = res.data.data;
            let queue = [];
            let id = 1;
            data["id"] = id++;
            queue.push(data);
            let innerTreeData = [];
            innerTreeData.push({
              id: data["id"],
              taskName: data["taskName"],
              fatherId: 0,
              time: data["execTime"],
              status: me.statusMap[data["status"]]
            });

            while (queue.length != 0) {
              let task = queue.shift();
              console.log(task["taskName"], task);
              let parents = task["parents"];
              if (parents != null) {
                parents.forEach(p => {
                  p["id"] = id++;
                  queue.push(p);
                  innerTreeData.push({
                    id: p["id"],
                    taskName: p["taskName"],
                    dependType: p["dependType"],
                    color: p["dependType"] == 1 ? "black" : "red",
                    fatherId: task["id"],
                    time: p["execTime"],
                    status: me.statusMap[p["status"]]
                  });
                  console.log(innerTreeData);
                });
              }
            }

            let tableTask = {};
            tableTask["taskname"] = data["taskName"];
            tableTask["remark"] = data["remark"];
            tableTask["utime"] = this.time();
            tableTask["hasWeakRelation"] =
              data["weakRelation"].length > 0 &&
              this.whiteList.indexOf(data["taskName"]) == -1;
            tableTask["treeData"] = innerTreeData;
            let replaceIndex = -1;
            this.tableData.forEach((task, index) => {
              console.log("task", task);
              if (task["taskname"] == taskName) {
                replaceIndex = index;
              }
            });
            console.log("index", replaceIndex);
            console.log("tableTask", tableTask);

            if (replaceIndex != -1) {
              this.$set(this.tableData, replaceIndex, tableTask);
            }
          } else {
            this.$message.error("");
          }
          loading.close();
        })
        .catch(error => {
          loading.close();
          this.$message.error("");
          console.error(error);
        });
    },
    refreshAllTree(index, scope) {
      let taskName = scope.row.treeData[0].taskName;

      let params = {
        taskName: taskName
      };
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      getTaskParentsWithoutFilter(params)
        .then(res => {
          if (res.data.code === 0) {
            this.initTask();
          }
          loading.close();
        })
        .catch(error => {
          loading.close();
          this.$message.error("");
          console.error(error);
        });
    },
    addTask() {
      this.$prompt("ETL", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          const loading = this.$loading({
            lock: true,
            text: "Loading",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)"
          });
          if (value != null) {
            addTask(value)
              .then(res => {
                console.log(res);
                if (res.data.code === 0) {
                  getEtlTask()
                    .then(res => {
                      console.log(res);
                      if (res.data.code === 0) {
                        let me = this;
                        let data = res.data.data;
                        if (data != null && data != undefined) {
                          this.tableData = [];
                          data.forEach(mainTask => {
                            let tableTask = {};
                            tableTask["taskname"] = mainTask["taskName"];
                            tableTask["remark"] = mainTask["remark"];
                            tableTask["utime"] = mainTask["utime"];
                            tableTask["hasWeakRelation"] =
                              mainTask["weakRelation"].length > 0 &&
                              this.whiteList.indexOf(mainTask["taskName"]) ==
                                -1;

                            let queue = [];

                            let id = 1;
                            mainTask["id"] = id++;
                            queue.push(mainTask);
                            let innerTreeData = [];
                            innerTreeData.push({
                              id: mainTask["id"],
                              taskName: mainTask["taskName"],
                              fatherId: 0,
                              time: mainTask["execTime"],
                              status: me.statusMap[mainTask["status"]]
                            });
                            console.log(innerTreeData);
                            while (queue.length != 0) {
                              let task = queue.shift();
                              console.log(task["taskName"], task);
                              let parents = task["parents"];
                              if (parents != null) {
                                parents.forEach(p => {
                                  p["id"] = id++;
                                  queue.push(p);
                                  innerTreeData.push({
                                    id: p["id"],
                                    taskName: p["taskName"],
                                    dependType: p["dependType"],
                                    color:
                                      p["dependType"] == 1 ? "black" : "red",
                                    fatherId: task["id"],
                                    time: p["execTime"],
                                    status: me.statusMap[p["status"]]
                                  });
                                });
                              }
                            }
                            tableTask["treeData"] = innerTreeData;
                            this.tableData.push(tableTask);
                          });
                        }
                      } else {
                        this.$message.error("");
                      }
                      loading.close();
                    })
                    .catch(error => {
                      loading.close();
                      this.$message.error("");
                      console.error(error);
                    });
                  this.$message.success("");
                  this.$set(this.tableData, 0, this.tableData[0]);
                } else if (res.data.code === 2) {
                  this.$message.error("");
                } else {
                  this.$message.error("");
                }
              })
              .catch(error => {
                this.$message.error("");
                console.error(error);
              });
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    getWhiteList() {
      getWhiteList()
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            let data = res.data.data;
            this.whiteList = [];
            this.whiteDropList = [];

            let id = 1;
            data.forEach(name => {
              this.whiteDropList.push({
                name: name,
                index: id++
              });
              this.whiteList.push(name);
            });
          } else {
            this.$message.error("");
          }
        })
        .catch(error => {
          this.$message.error("");
          console.error(error);
        });
    },
    addWhiteList(index, scope) {
      let taskName = scope.row.treeData[0].taskName;
      addWhiteList(taskName)
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            this.$message.success("");
            this.getWhiteList();
            scope.row.hasWeakRelation = false;
            this.$set(this.tableData, scope.row.index, scope.row);
          } else {
            this.$message.error("");
          }
        })
        .catch(error => {
          this.$message.error("");
          console.error(error);
        });
    },
    handleCommand(command) {
      removeWhiteList(command)
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            this.$message.success("");
            this.getWhiteList();
            this.initTask();
          } else {
            this.$message.error("");
          }
        })
        .catch(error => {
          this.$message.error("");
          console.error(error);
        });
    },
    time() {
      var time = new Date();
      var y = time.getFullYear();
      var M = time.getMonth() + 1;
      var d = time.getDate();
      var h = time.getHours();
      var m = time.getMinutes();
      var s = time.getSeconds();
      return (
        y +
        "-" +
        this.addZero(M) +
        "-" +
        this.addZero(d) +
        " " +
        this.addZero(h) +
        ":" +
        this.addZero(m) +
        ":" +
        this.addZero(s)
      );
    },
    addZero(m) {
      return m < 10 ? "0" + m : m;
    }
  }
};
</script>

