    handleClick(value) {
      console.log(value);
      if (value.data.des != undefined) {
        window.open("http://xt.sankuai.com/workbench/task/" + value.data.des);  
      }
      
    },
    copy(value) {
      console.log(value);
      let that = this;
      if (value.data.des != undefined) {
        this.$copyText(value.data.des + "\n" + value.data.owner).then(function (e) {
          that.$message.success("");
          console.log(e)
        }, function (e) {
          that.$message.success("");
          console.log(e)
        })
      }
      
    },
    compare(v1, v2) {
      if (v1.level !== v2.level) {
        return v1.deep - v2.deep;
      }
      if (v1.parent === v2.parent) {
        return v1.id - v2.id;
      }
      return this.compare(v1.parent, v2.parent);
    },
    async change(value) {
      console.log("style",this.$refs);
      if (this.leafTasksValue.length == 0) {
        this.graphOptions = {};
      } else {
        await this.initTaskGraph(this.leafTasksValue);
      }
      console.log("refs",this.$refs, this.$graphArea);
      this.chartStyle = 'width: ' + this.$graphArea.width + 'px;height: ' + this.$graphArea.height +'px;margin-top: 20px;'
      console.log("chartStyle", this.chartStyle);
      await new Promise((resolve) => setTimeout(resolve, 100));
      this.$refs.chart.refresh();
    },
