      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });

      await getAllLeafTask()
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            let data = res.data.data;
            if (data != null && data != undefined) {
              this.leafTasks = [];

              data.forEach(task => {
                this.leafTasks.push({
                  key: task.id,
                  name: task.taskName,
                  value: task.taskName
                });
              });

              console.log(this.leafTasks);
            }
          } else {
            this.$message.error("");
          }
          loading.close();
        })
        .catch(error => {
          loading.close();
          this.$message.error("");
          console.error(error);
        });
    },
