                let graphNode = {
                  name: "" + node.id,
                  des: node.taskName,
                  owner: node.owner,
                  symbolSize: 200,
                  category:
                    taskNames.indexOf(node.taskName) != -1
                      ? 2 :
                      node.isRoot ? 3:
                      node.inProject
                      ? 0
                      : 1
                };

                nodes.push(graphNode);
              });
              
              let edges = [];
              data.edges.forEach(edge => {
                let graphEdge = {
                  source: "" + edge.source,
                  target: "" + edge.target,
