                      onclick: async function (){
                          console.log(that.graphOptions);
          
                        
                          that.chartStyle = 'width: ' + that.$graphArea.width + 'px;height: ' + that.$graphArea.height +'px;margin-top: 20px;'
                          that.graphOptions.series[0].zoom = 1;
                          await new Promise((resolve) => setTimeout(resolve, 100));
                          that.$refs.chart.refresh();
                          console.log(that.chartStyle);
                      },
      
                    },
                    // 
                    saveAsImage: {
                      show: true,
                      pixelRatio : 3
                    },
                    myZoomUp: {
                      show: true,
                      title: '',
                      icon: 'path://M0,-1 V1 M-1,0 H1',
                      onclick: function (){
                          console.log(that.graphOptions);
                          that.graphOptions.series[0].zoom = 0.1 + that.graphOptions.series[0].zoom;
                      },
                    },
                    
                    myZoomDown: {
                      show: true,
