                      icon: 'path://M877.4 528.2H148.7c-10.5 0-19.1-8.6-19.1-19.1s8.6-19.1 19.1-19.1h728.7c10.5 0 19.1 8.6 19.1 19.1s-8.5 19.1-19.1 19.1z',
                      onclick: function (){
                          console.log(that.graphOptions);
                          that.graphOptions.series[0].zoom =  that.graphOptions.series[0].zoom - 0.1;
                      }
                    },
                    // myZoomUpChart: {
                    //   show: true,
                    //   title: '',
                    //   icon: 'path://M912 262v500c0 41.3-14.7 76.7-44 106s-64.6 44-106 44H262.1c-41.3 0-76.7-14.7-106-44s-44-64.6-44-106V262c0-41.3 14.7-76.7 44-106s64.6-44 106-44h500c41.3 0 76.7 14.7 106 44 29.2 29.3 43.9 64.7 43.9 106zM778.6 545.4v-66.7c0-9-3.3-16.9-9.9-23.4-6.6-6.6-14.5-9.9-23.4-9.9H578.7V278.6c0-9-3.3-16.9-9.9-23.4-6.6-6.6-14.5-9.9-23.4-9.9h-66.7c-9 0-16.9 3.3-23.4 9.9s-9.9 14.5-9.9 23.4v166.6H278.7c-9 0-16.9 3.3-23.4 9.9-6.6 6.6-9.9 14.5-9.9 23.4v66.7c0 9 3.3 16.9 9.9 23.4 6.6 6.6 14.3 9.9 23.4 9.9h166.7v166.7c0 9 3.3 16.9 9.9 23.4 6.6 6.6 14.3 9.9 23.4 9.9h66.7c9 0 16.9-3.3 23.4-9.9 6.6-6.6 9.9-14.5 9.9-23.4V578.6h166.6c9 0 16.9-3.3 23.4-9.9 6.6-6.6 9.9-14.4 9.9-23.3z',
