    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (row["hasWeakRelation"]) {
        return "warning-row";
      }
      return "";
    },
    showEditRemark(row, column, cell, event) {
      console.log(row, column, cell, event);
      if (!row.showEdit && column.property == "remark") {
        row.showEdit = true;
        this.$set(this.tableData, row.index, row);
        console.log("refs", this.$refs[`remark${row.index}`]);
        this.$nextTick(function() {
          this.$refs[`remark${row.index}`].$el.querySelector("input").focus();
        });
        this.$set(this.tableData, row.index, row);
      }
    },
    handleUpdate(index, row) {
      console.log(row);
      row.showEdit = false;
      if (row.taskname != null && row.remark != null)
        updateRemark(row.taskname, row.remark)
          .then(res => {
            console.log(res);
            if (res.data.code === 0) {
              this.$message.success("");
            } else {
              this.$message.error("");
            }
          })
          .catch(error => {
            this.$message.error("");
            console.error(error);
          });
      this.$set(this.tableData, row.index, row);
    },
    time() {
      var time = new Date();
      var y = time.getFullYear();
      var M = time.getMonth() + 1;
      var d = time.getDate();
      var h = time.getHours();
      var m = time.getMinutes();
      var s = time.getSeconds();
      return (
        y +
        "-" +
        this.addZero(M) +
        "-" +
        this.addZero(d) +
        " " +
        this.addZero(h) +
        ":" +
        this.addZero(m) +
        ":" +
        this.addZero(s)
      );
    },
    addTask() {
      this.$prompt("ETL", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          if (value != null) {
            const loading = this.$loading({
              lock: true,
              text: "Loading",
              spinner: "el-icon-loading",
              background: "rgba(0, 0, 0, 0.7)"
            });
            addTaskDepend(value)
              .then(res => {
                console.log(res);
                if (res.data.code === 0) {
                  this.initTask();
                  this.$message.success("");
                } else {
                  this.$message.error("");
                }
                loading.close();
              })
              .catch(error => {
                this.$message.error("");
                console.error(error);
                loading.close();
              });
          }
        })
        .catch(error => {
          console.error(error);
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    addZero(m) {
      return m < 10 ? "0" + m : m;
    },
    secondToHour(value) {
      let hour = Math.floor(value / 3600);
      let minute = Math.floor((value - hour * 3600) / 60);
      let second = value - hour * 3600 - minute * 60;
      let texts = [
        this.addZero(hour),
        this.addZero(minute),
        this.addZero(second)
      ];

      return texts.join(":");
    },
    getChartStyle() {
      console.log("style",this.$refs);
      console.log(this.$refs.chart.height, this.$refs.chart.width);
      return 'width: ' + this.$refs.chart.width + ';height: ' + this.$refs.chart.height +'px;margin-top: 20px;'
    }
